.stars {
  flex-wrap: wrap;
  display: flex;

  &__star {
    height: 16px;
    width: 16px;
    margin-right: 4px;
    background: url(../images/star.svg) no-repeat center;
  }
}

.stars--5 .stars__star,
.stars--1 .stars__star:first-child,
.stars--2 .stars__star:not(:nth-child(1n + 3)),
.stars--3 .stars__star:not(:nth-child(1n + 4)),
.stars--4 .stars__star:not(:nth-child(1n + 5)) {
  background-image: url(../images/star-active.svg);
}
